ol {
  list-style: none;
  counter-reset: item;
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
}
li {
  counter-increment: item;
  margin-bottom: 25px;
}
li:before {

  content: counter(item);
  background: rgb(0, 0, 0);
  border-radius: 100%;
  color: rgb(255, 255, 255);
  width: 2.8em;
  height: 2.8em;
  text-align: center;
  display: inline-block;
}
